import { Component, OnInit } from '@angular/core'; 

@Component({
  selector: 'app-sd-light-source',
  templateUrl: './sd-light-source.component.html',
  styleUrls: ['./sd-light-source.component.scss']
})
export class SdLightSourceComponent implements OnInit {

  // private colorPallets=['red','pink','purple','indigo','blue','cyan','teal','green','lime','yellow','amber','orange','deep-orange','brown','grey'];
  // private namedPallets=['dark','secondary','neutral','text','highlight','success','action','alert','warning'];
  // private texts = ['title-large','title-1','title-2','title-3','subtitle-1','subtitle-2','body-1','body-2','caption-1','caption-2','button'];
  // // private types = ['base','ultradark','darkest','dark','light','lightest','ultralight'];
  // // 
   private ls=['top','topright','right','bottomright','bottom','bottomleft','left','topleft','front'];


  private installMsg:any;
  private importMsg:any;
  private appliedClasses:any = ' sd--lightsource-top';
  private currentClass:any = 'top';
  private extraClass:any = '';
  private extraClassEx:any = '';
  private currentClassExpanded:any = ' sd--lightsource-top';
  private appliedCir:any = '';


  private appliedTextClasses:any = ' sd--lightsource-top';
  private currentTextClass:any = 'top';
  private extraTextClass:any = '';
  private extraTextClassEx:any = '';
  private currentTextClassExpanded:any = ' sd--lightfrom-top';
  private appliedTextCir:any = '';

  constructor() { }


  ngOnInit() { 
    this.installMsg =`
    npm i sd-light-source-css`

    this.importMsg=`
    /**** include it in the angular.json file ****/

    "styles": [
      "node_modules/sd-light-source-css/sd-light-source.min.css"
    ],`
  }

  setShadowClass(item:string,type:string){
    if(type=='box'){

      this.currentClass=item;
      this.currentClassExpanded = 'sd--lightsource-'+item ;
      this.appliedClasses = this.currentClassExpanded+ '  '+this.extraClass + ' '+this.appliedCir ;

    }

    if(type=='text'){

      this.currentTextClass=item;
      this.currentTextClassExpanded = 'sd--lightsource-'+item ;
      this.appliedTextClasses = this.currentTextClassExpanded+ '  '+this.extraTextClass + ' '+this.appliedTextCir ;

    }

  
  }

  addToClass(item,type){
    if(type=='box'){
      this.extraClass =item;
      this.appliedClasses = this.currentClassExpanded + '  '+item + ' '+this.appliedCir ;;
    }

    if(type='text'){
      this.extraTextClass =item;
      this.appliedTextClasses = this.currentTextClassExpanded + '  '+item + ' '+this.appliedTextCir ;;
    }
   

  }

  addCircleClass(item){

    if(this.appliedCir == ''){
      this.appliedCir = 'sd--'+item;
    }else{
      this.appliedCir = '';
    } 
    
    this.appliedClasses = this.currentClassExpanded + '  '+this.extraClass + ' '+this.appliedCir;
    
  }
 
}
