import { Component, OnInit } from '@angular/core';
import { DemoLinkService } from 'src/app/services/demo-link.service';
import { GirdDataService } from 'src/app/services/gird-data.service';

@Component({
  selector: 'app-css-library-page-container',
  templateUrl: './css-library-page-container.component.html',
  styleUrls: ['./css-library-page-container.component.scss']
})
export class CssLibraryPageContainerComponent implements OnInit {

  private subHeaderContent:any;
  private componentsInfo:any;
  private showCards:any;
  private componentData:any;

//   @HostListener('window:scroll', ['$event']) getScrollHeight(event) {
//     console.log(window.pageYOffset, event);
//  }

  constructor(private _dl:DemoLinkService,private _ds:GirdDataService) { }

  ngOnInit() {

    this.showCards='cards';

    this.subHeaderContent = {
      icon: 'fa fa-palette',
      info: [
        'Easy to use prebuilt css libraries',
        'Install using NPM',
        'Apply in your project',
        'Implement otherwise time consuming styles out of the box'
      ],
      showIconBig:true
    }

    // this.subHeaderContent = {
    //   icon: 'fa fa-hdd',
    //   info: [
    //     'Create customised JSON that suits your APP ',
    //     'Create INSERT SQL statements and Test Data',
    //     'Create API with querystring to use directly in your APP  ',
    //     'Use Default Grab and Go APIs'
    //   ],
    //   showDemoBig:true, 
    //   demoLinkText:"See Tutorial Instead",
    //   videoLink:"wkLgQIfdbn0"
    // }

    this.componentsInfo= this._ds.getDataForCard().cssCards;


  }

  onInconClick(){
    this.showCards = 'cards';
  }


  onCardClick(item){ 

    if(item.disabled)return;

    this.showCards = item.label;
 
    let tempLink ;
    let showYt;
    
    switch(this.showCards) {
      case "Sd-Light-Source.CSS":
        tempLink =this._dl.getLinks().EASY_LIGHSOURCE_DEMO;
        showYt=true;
        break;
      case "Sd-Material-Pallets.CSS":
        tempLink=this._dl.getLinks().EASY_MATERIALPALLET_DEMO ;
        showYt=true;
        break;
      case "Sd-Pixeltorem.CSS":
        tempLink=this._dl.getLinks().EASY_PIXELTOREM_DEMO;
        showYt=true;
        break;
      default:
        tempLink="su9EVeHqizY"
    }

    this.componentData= {info:[item.label],videoLink:tempLink,showDemoSmall:showYt,demoLinkText:" Demo", showIconSmall:true, icon: 'fa fa-palette'} 

 
  }

}
